<template>
    <div class="msite">
        <header>
            <router-link to="/search">
                <i class="el-icon-search i1"></i>
            </router-link>
            <router-link to="/home">
                <span class="s1 ellipsis">{{position.name}}</span>
            </router-link>
            <router-link to="/login" v-if="isShow1" class="s2">
                <span>登录|注册</span>
            </router-link>
            <router-link to="/profile" v-if="!isShow1" class="s2">
                <i class="el-icon-user"></i>
            </router-link>
        </header>
        <nav >
            <div class="nav1">
            <swiper :options="swiperOption" class="nav2">
                <swiper-slide class="swiper-slide">
                    <div v-for="pro in index_entry1">
                        <div @click="toFood(pro)" class="btn">
                            <img :src="'https://cube.elemecdn.com'+pro.image_url" class="img">
                            <p class="p1">{{pro.title}}</p>
                        </div>
                    </div>
                </swiper-slide>
                <swiper-slide class="swiper-slide">
                    <div v-for="pro in index_entry2">
                        <div @click="toFood(pro)" class="btn">
                            <img :src="'https://cube.elemecdn.com'+pro.image_url" class="img">
                            <p class="p1">{{pro.title}}</p>
                        </div>
                    </div>
                </swiper-slide>
            </swiper>
            <div class="swiper-scrollbar"></div>
            <div class="swiper-pagination"></div>
            </div>
        </nav>
        <section>
            <div class="shop_header">
                <i class="el-icon-s-shop"></i><span>附近商家</span>
            </div>
            <ul>
                <li v-for="s in shopping" class="sli" @click="toShop(s)">
                    <img :src="'https://elm.cangdu.org/img/'+s.image_path" class="simg">
                    <div class="sli1"><h4><span>品牌</span>{{s.name}}</h4>
                    <ul><li v-for="a in s.supports">
                        <span>{{a.icon_name}}</span>
                    </li></ul></div>
                    <div class="sli2"><el-rate
                            v-model="s.rating"
                            disabled
                            text-color="#ff9900"
                            class="rating"
                            >
                    </el-rate><span class="ratingnumber">{{s.rating}}</span><span class="recent">月销售：{{s.recent_order_num}}单</span><span class="rating_right"><span>{{s.delivery_mode?s.delivery_mode.text:''}}</span><span>{{s.supports[1]?s.supports[1].name:''}}</span></span></div>
                    <div class="sli3"><span>￥{{s.float_minimum_order_amount}}起送 / 配送费约￥{{s.float_delivery_fee}}</span><p>{{s.distance}} / <span>{{s.order_lead_time}}</span></p></div>
                </li>
            </ul>
        </section>
        <footer>
            <router-link to=""><div class="f1"><i class="el-icon-eleme"></i><p>外卖</p></div></router-link>
            <router-link to="/search"><div><i class="el-icon-search"></i> <p>搜索</p></div></router-link>
            <router-link to="/order"><div><i class="el-icon-tickets"></i><p>订单</p></div></router-link>
            <router-link to="/profile"><div><i class="el-icon-user"></i> <p>我的</p></div></router-link>
        </footer>
    </div>

</template>

<script>
    import 'swiper/dist/css/swiper.css'
    import { swiper, swiperSlide } from 'vue-awesome-swiper'
    export default {
        name: "Msite",
        components: {
            swiper,
            swiperSlide
        },
        data(){
            return{
                geohash:"",
                latitude:"",
                longitude:"",
                position:"",
                index_entry1:[],
                index_entry2:[],
                shopping:[],
                masite:[],
                isShow1:true,
                swiperOption: {
                    loop: true,
                    autoplay: 2000,
                    autoHeight: true,
                    pagination : '.swiper-pagination',
                    paginationClickable :true,
                    autoplayDisableOnInteraction:false,
                },
            }
        },
        created(){
            if(localStorage.getItem("user_id")){
                this.isShow1 = false;
            }else {
                this.isShow1 = true;
            }
            this.msite = JSON.parse(localStorage.getItem("msite"));
            //获取坐标位置
            this.axios.get("/v2/pois/"+this.msite.geohash).then(res=>{
                this.position = res;
                console.log(res);
            }).catch(err=>{});

            //获取食品分类
            this.axios.get("/v2/index_entry").then(res=>{
                for(let i =0;i<res.length;i++){
                    if(i<8){
                        this.index_entry1.push(res[i]);
                    }else {
                        this.index_entry2.push(res[i]);
                    }
                }
                // console.log(res);
            }).catch(err=>{});

            //获取商铺列表
            this.axios.get("/shopping/restaurants?",{
                params:{
                    latitude:this.msite.latitude,
                    longitude:this.msite.longitude
                }
            }).then(res=>{
                this.shopping = res;
                // console.log(res);
            }).catch(err=>{});
        },
        methods:{
            //跳转到food页并传值
            toFood(f){
                this.$router.push({name:'food',params:{title:f.title}});
            },
            toShop(s){
                this.$router.push("/shop");
                localStorage.setItem("shop",JSON.stringify(s));
            }
        }
    }
</script>

<style scoped>
    .msite{
        width: 100%;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    .i1{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .s1{
        width: 8rem;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: .8rem;
        color: #fff;
        text-align: center;
    }
    .s2{
        right: .4rem;
        font-size: .65rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    nav{
        width: 100%;
        padding-top: 2.1rem;
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        box-sizing: border-box;
        height: 10.6rem;
        position: relative;
    }
    .nav2{
        width: 100%;
        height: 100%;
    }


    .swiper-slide{
        width: 100%;
        height: 8rem;
    }
    .swiper-slide>div{
        width: 25%;
        float: left;
        text-align: center;
    }
    .img{
        margin:0.4rem 0 0.5rem;
        width: 1.8rem;
        height: 1.8rem;
    }
    .p1{
        text-align: center;
        font-size: .55rem;
        color: #666;
    }
    .swiper-pagination{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 1rem;
        margin: auto;
    }
    section{
        width: 100%;
        margin-top: .4rem;
        border-top: .025rem solid #e4e4e4;
        background-color: #fff;
        padding-bottom: 1.95rem;
    }
    .shop_header>i{
        color: #999;
        margin-left: .6rem;
        margin-bottom: 0.4rem;
        vertical-align: middle;
        width: .6rem;
        height: .6rem;
    }
    .shop_header>span{
        margin-left: 0.6rem;
        color: #999;
        font: .55rem/1.6rem Microsoft YaHei;
    }
    .sli{
        border-bottom: .025rem solid #f1f1f1;
        padding: .7rem .4rem;
    }
    .simg{
        width: 2.7rem;
        height: 2.7rem;
        display: block;
        margin-right: .4rem;
        float: left;
    }
    .sli1{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 0.6rem;
    }
    .sli1 h4 span{
        display: inline-block;
        font-size: .5rem;
        line-height: .6rem;
        color: #333;
        background-color: #ffd930;
        padding: 0 .1rem;
        border-radius: .1rem;
        margin-right: .2rem;
    }
    .sli1 h4{
        width: 8.5rem;
        color: #333;
        padding-top: .01rem;
        font: .65rem/.65rem PingFangSC-Regular;
        font-weight: 700;
    }

    .sli1 ul{
        display: flex;
        transform: scale(.8);
        margin-right: -.3rem;
    }
    .sli1 ul li{
        font-size: .5rem;
        color: #999;
        border: .025rem solid #f1f1f1;
        padding: 0 .04rem;
        border-radius: .08rem;
        margin-left: .05rem;
    }
    .sli2{
        margin-top: .52rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 0.6rem;
    }
    .ratingnumber{
        font-size: .4rem;
        color: #ff6000;
    }
    .recent{
        transform: scale(.8);
        margin-left: -.2rem;
        font-size: .4rem;
        color: #666;
    }
    .rating_right{
        display: flex;
        align-items: center;
        transform: scale(.7);
        min-width: 5rem;
        justify-content: flex-end;
        margin-right: -.8rem;
    }
    .rating_right>span{
        font-size: .4rem;
        padding: .04rem .08rem 0;
        border-radius: .08rem;
        margin-left: .08rem;
    }
    .rating_right>span:nth-child(1){
        color: #fff;
        background-color: #3190e8;
        border: .025rem solid #3190e8;
    }
    .rating_right>span:nth-child(2){
        color: #3190e8;
        border: .025rem solid #3190e8;
    }
    .sli3{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 0.6rem;
        margin-top: .52rem;
        font-size: .5rem;
        color: #333;

    }
    .sli3>span{
        transform: scale(.9);
        font-size: .5rem;
        color: #666;
    }
    .sli3>p{
        transform: scale(.9);
        color: #999;
    }
    .sli3>p>span{
        color:#3190e8;
    }
    footer{
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 1.95rem;

        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    footer div{
        width: 25%;
        height: 1.95rem;
        text-align: center;
        align-items: center;
        float: left;
    }

    footer i{
        width: .8rem;
        height: .8rem;
        margin: .3rem 0 .15rem;
        color: #666;
    }
    .f1 i{
        color:#3190e8;
    }
    footer p{
        font-size: .45rem;
        color: #666;
    }
</style>
<style>
    .swiper-pagination-bullet{
        margin: 0 0.2rem;
    }
    .rating i{
        margin-right: -0.1rem;
        font-size: 0.4rem;
        margin-bottom: 0.25rem;
    }
</style>